/* Submission form */
.submission-main-card {
    margin-top: 2%;
    width: 400px;
    text-align: center;
    border: 1px solid lightgray;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 5px;
    background-color: #35393e;
}

#sub-title {
    height: 80px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color: darkred;
    color: white;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.submission-form-list {
    height: 620px;
    width: 100%;
    overflow: scroll;
}


.submission-list-item {
    background-color: white;
    padding: 3%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    width: 96%;
    margin: 2% auto 2% auto;
}

#theMovie {
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 100%;
    text-align: center;
    align-items: center;
    font-weight: bold;
    border-bottom: 1px solid darkred;
    margin-bottom: 1%;
}

#imdb, #cast, #hd {
    margin-bottom: 5%;
}

#theInfo {
    border-bottom: 1px solid darkred;
    margin-bottom: 2%;
}
.filmDataSend {
    text-align: left;
}
#poster-outer {
    padding: 2%;
    text-align: center;
}

.img-poster {
    display: block;
    width: 100%;
    height: 100%;
}

#library-row {
    display: flex;
    justify-content: center;
}

h6 {
    text-align: center;
}


#main-about-container {
    border: 1px solid lightgray;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 5px;
    padding: 3%;
    margin: 3% 0 10%;
    
}

#about-col1, #about-col2 {
    text-align: justify;
}
.parent-market-logos {
    border: 1px solid blue;
    width: 100%;
}


.market-logos {
    /* border: 1px solid black; */
    height: 150px;
    margin: 10% 10% 0 10%;
    overflow: hidden;
    padding: 5%;
}


.logo-image {
    display: block;
    width: 100%;
    height: 100%;
}


.market-text {
    /* border: 1px solid black; */
    text-align: center;
    height: 100px;
    margin: 0 10% 0 10%;
    overflow: hidden;
    padding: 0 5% 5% 5%;
    line-height: 10px;
}



form {
    /* background-image: url("/assets/images/photos/TB5A2801.jpg"); */
    /* background-size: cover; */
    overflow: scroll;
}

#main-contact-container {
    border: 1px solid lightgray;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 5px;
    padding: 3%;
    margin: 3% 0 10%;
    
}

#frmbtn {
    border: none;
    width: 100%;
    background-color: #b53b3e;
    font-weight: bolder;
}

#filmSubTitle {
    margin-bottom: 2%;
}

.submit-info {
    line-height: 20px;
}

#sales-inquiries, #acquisitions {
    margin-bottom: 10%;
    line-height: 10px;
}

#to-submit {
    margin-bottom: 3%;
    text-align: justify;
}

.avails-outer {
    margin-top: 2%;
    border: 1px solid lightgray;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 5px;
    height: 700px;
    overflow: scroll;
    /* overflow: auto; */
}

.avails-header {
    background-color: darkred;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    display: flex;
    justify-content: space-between;
    color: white;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    padding: 0 2% 0 2%; 
}

.avails-upper {
    background-color: darkred;
}

#db-title {
    margin: 0px;
    color: white;
    font-weight: bold;
} 

.add-movie-button {
    background-color:#35393e;
    color: white;
    border: lightgray;
}

.add-movie-button:hover {
    background-color: lightgray;
    color: black;
}

.delete-btn, .update-btn {
    background-color:#35393e;
    color: white;
}

.delete-btn:hover {
    background-color: lightgray;
    color: black;
}

.update-btn:hover {
    background-color: lightgray;
    color: black;
}

.modal-dialog {
    height: 806px;
    overflow: scroll;
    border-radius: 10px;
}
.footer {
  margin-top: 32%;
}

#admin-footer { 
  color: black !important;
  margin-top: 100%;
  margin-bottom: 0;
}

.footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}
